import React, { useState, useEffect } from 'react';
import './App.css';
import axios from "axios"
import * as XLSX from "xlsx";

// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: "AIzaSyBKGOMyHVPHaAwRmlod2np8YFwWqxO8T6M",
  authDomain: "vlh-tuyendung.firebaseapp.com",
  projectId: "vlh-tuyendung",
  storageBucket: "vlh-tuyendung.appspot.com",
  messagingSenderId: "736260500988",
  appId: "1:736260500988:web:6b40bc98304c39a3ee1679",
  measurementId: "G-D5H2SLLPLG",
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

function App() {
  const [items, setItems] = useState([]);
  const [smsText, setSmsText] = useState("");
  // const [exportedData, setExportedData] = useState(false);

  useEffect(() => {
    fetchDataFromMongoDB();
    console.log(items);
  }, []);

  const fetchDataFromMongoDB = async () => {
    try {
      const response = await axios.get(
        "https://tuyendung-vlh.onrender.com/api/admin/users"
      );
      setItems(response.data);
      console.log(response.data);
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  const renderTable = () => {
    return (
      <table>
        <thead>
          <tr>
            <th>#</th>
            <th>Họ và Tên</th>
            <th>Email</th>
            <th>Số điện thoại</th>
            <th>Ngày Sinh</th>
            <th>Giới tính</th>
            <th>Căn cước công nhân</th>
            <th>Trình độ học vấn</th>
            <th>Vị trí ứng tuyển</th>
          </tr>
        </thead>
        <tbody>
          {items.slice(0).map((item, index) => (
            <tr key={index}>
              <td>{index + 1}</td>
              <td>{item.fullname}</td>
              <td>{item.email}</td>
              <td>{item.numberPhone}</td>
              <td>{item.date}</td>
              <td>{item.gender}</td>
              <td>{item.cccd}</td>
              <td>{item.tdhv}</td>
              <td>{item.vtut}</td>
              <textarea
                id={item._id}
                value={smsText}
                onChange={(e) => setSmsText(e.target.value)}
                required
              />
              <br />
              <button
                value={item.numberPhone}
                onClick={() => handleSendSMS(item.numberPhone)}
              >
                Gửi SMS
              </button>
            </tr>
          ))}
        </tbody>
      </table>
    );
  };

  const exportToExcel = () => {
    console.log(items);
    const wb = XLSX.utils.book_new();
    const header = [
      // "Họ và Tên",
      // "Email",
      // "Số điện thoại",
      // "Ngày sinh",
      // "Giới tính",
      // "CCCD",
      // "Trình độ học vấn",
      // "Vị trí ứng tuyển",
    ];
    const ws = XLSX.utils.aoa_to_sheet([[header]]);
    XLSX.utils.sheet_add_json(ws, items, { header });
    XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
    XLSX.writeFile(wb, "mongodb_data.xlsx");
  };

  const deleteData = (id) => {
    console.log(id);
    try {
      axios.delete(`https://tuyendung-vlh.onrender.com/api/data/${id}`);
      console.log("Data deleted successfully");
    } catch (error) {
      console.error("Error deleting data:", error);
    }
  };

  const handleSendSMS = (numberPhone) => {
    // Gửi yêu cầu gửi tin nhắn tới backend
    axios
      .post("https://tuyendung-vlh.onrender.com/api/admin/sendSMS", {
        numberPhone,
        smsText,
      })
      .then((response) => {
        console.log(` sdt nhận: ${numberPhone}`);
        console.log(response.data);
        setSmsText();
        alert("Tin nhắn đã được gửi!");
      })
      .catch((error) => {
        console.error("Đã có lỗi xảy ra khi gửi tin nhắn:", error);
        alert("Đã có lỗi xảy ra khi gửi tin nhắn!");
      });
  };

  return (
    <div className="container">
      <h1>Danh sách người dùng</h1>
      <button onClick={exportToExcel}>Export to Excel</button>
      {/* {renderTable()} */}
      <ul>
        {items.map((item) => (
          <li key={item._id}>
            <h2>Họ và tên: {item.fullname}</h2>
            <p>Email: {item.email}</p>
            <p>Số Điện thoại: {item.numberPhone}</p>
            <p>Ngày sinh:{item.date}</p>
            <p>Giới tính: {item.gender}</p>
            <p>Căn cước công dân: {item.cccd}</p>
            <p>Vị trí ứng tuyển: {item.vtut}</p>
            <p>Nội dung SMS</p>
            <textarea
              id={item._id}
              value={smsText}
              onChange={(e) => setSmsText(e.target.value)}
              required
            />
            <br />
            <button
              value={item.numberPhone}
              onClick={() => handleSendSMS(item.numberPhone)}
            >
              Gửi SMS
            </button>
            {/* <img src={item.image} alt={item.fullname} /> */}
            <button onClick={() => deleteData(item._id)}>Xóa</button>
          </li>
        ))}
      </ul>
      ;
    </div>
  );
}

export default App;







